import React, { useContext } from "react"
import AppContext from '../contextAPI/AppContext'
import { Link, navigate } from "gatsby"
import SVLogo from '../images/SVLogo.jpeg'
import SEO from "../components/seo"
import styles from "./index.module.css"

const IndexPage = () => {
  const receivedAppContext = useContext(AppContext)
return (
  <div>
    <SEO title="Home" />
    <img src={SVLogo} classNames={styles.logo}/>
    <p onClick={() => {
      receivedAppContext.updateLanguage("GRK")
      receivedAppContext.updateVersion("GNA")
      navigate("/Reader/GRK/GNA/Luk/1")}}
    >GREEK</p>
    <p onClick={() => {
      receivedAppContext.updateLanguage("ENG")
      receivedAppContext.updateVersion("NLT")
      navigate("/Reader/ENG/NLT/Luk/1")}}
    >ENGLISH: NLT</p>
    <p onClick={() => {
      receivedAppContext.updateLanguage("ENG")
      receivedAppContext.updateVersion("WEB")
      navigate("/Reader/ENG/WEB/Luk/1")}}
    >ENGLISH: WEB</p>
  </div>
)
}

export default IndexPage
